$h2:  rgb(75, 75, 75);
$blue: #98b5cc;
$yellow: #ffcc00;
$outline: rgba(#fff, .4);
$shadow: rgba($yellow, .5);

.error404-title {
	font-family: 'CoreCircus', sans-serif;
	font-size: 5vw;
	text-align: center;
	line-height: 1;
    margin: 0;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	position: absolute;
	color: $h2;
	letter-spacing: -.5rem;
	
	&:before {
		content: attr(data-heading);
		position: absolute;
		overflow: hidden;
		color: $yellow;
		width: 100%;
		z-index: 5;
		text-shadow: none;
		left: 78%;
		text-align: left;
		animation: flicker 3s linear infinite;
    }
    
}

.error404-text {
    font-family: 'CoreCircus', sans-serif;
	font-size: 1vw;
	text-align: center;
	line-height: 1;
	margin: 0;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	position: absolute;
    color: $h2;
    width: 100%;
}

@keyframes flicker {
	0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
		opacity: .99;
		text-shadow: -1px -1px 0 $outline, 1px -1px 0 $outline, -1px 1px 0 $outline, 1px 1px 0 $outline, 0 -2px 8px, 0 0 2px, 0 0 5px #ff7e00, 0 0 15px #ff4444, 0 0 2px #ff7e00, 0 2px 3px #000;
	}
	20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
		opacity: 0.4;
		text-shadow: none;
	}
}

